<template>
  <div>
    <top/>
    <!--<topMember :index="1"></topMember>-->
    <div class="box mar over" style="width:1260px;">
        <div class="fffbg pull-left" style="width:910px;">
          <div class="mainH">
              <div class="mainHICO"></div><span class="pull-left">礼品商城</span>
          </div>
          <div class="pad15">
              <el-row class="shaixuan">
                  <el-col :span="8">
                        <div @click="paixu('price')">
                        价格
                        <span class="caret-wrapper"><i class="sort-caret ascending" :style="{'border-bottom-color':Hovcolor}"></i><i class="sort-caret descending" :style="{'border-top-color':Hovcolor2}"></i></span>
                        </div>
                  </el-col>
                  <el-col :span="8">
                      <div @click="paixu('weight')">
                        重量
                        <span class="caret-wrapper"><i class="sort-caret ascending" :style="{'border-bottom-color':Hovcolor3}"></i><i class="sort-caret descending" :style="{'border-top-color':Hovcolor4}"></i></span>
                      </div>
                  </el-col>
                  <el-col :span="8" >
                      <div @click="paixu('saleCount')">
                        销量
                        <span class="caret-wrapper"><i class="sort-caret ascending" :style="{'border-bottom-color':Hovcolor5}"></i><i class="sort-caret descending" :style="{'border-top-color':Hovcolor6}"></i></span>
                      </div>
                  </el-col>
              </el-row>
              <el-row class="shoplist" :gutter="15">
                  <el-col :span="6" v-for="(item,index) in list" :key="index">
                    <img @click="detail(item.id)" :src="item.images.split(',')[0]">
                    <div class="shoptitle">{{item.name}}</div>
                    <div class="mar5 over"><span class="red pull-left">￥<strong class="f20">{{item.price/100}}</strong></span><el-button @click="add(item,1)" class="pull-right" type="primary" size="mini">加入礼品包</el-button></div>
                    <div class="mar5 over shopDes">
                         <span class="pull-left">重量：{{item.weight/1000}}kg</span>
                         <span class="pull-right">库存：{{item.amount}}</span>
                    </div>
                  </el-col>
              </el-row>
          </div>
          <div class="pageDiv">
            <el-pagination
            background
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[16, 32, 64, 96]"
              :page-size="size"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total">
            </el-pagination>
          </div>
        </div>
        <div class="fffbg pull-right" style="width:330px;">
            <table width="100%" class="gwc">
              <tr>
                <th>产品</th>
                <th>单价</th>
                <th>重量</th>
                <th>数量</th>
                <th>总价</th>
              </tr>
              <tr v-for="(item,index) in shopcar">
                <td><img style="width:40px;height:40px;" :src="item.images.split(',')[0]"></td>
                <td>￥{{item.price/100}}</td>
                <td>{{item.weight/1000}}kg</td>
                <td>{{item.num}}
                  <!--<el-input-number style="width:90px" @change="changeNum(item.id,item.num)"  v-model="item.num" :min="1" :max="item.amount" size="mini"></el-input-number>-->
                </td>
                <td>
                    <div>￥{{item.total}}</div>
                    <i @click="del(index)" class="el-icon-delete deleteBt"></i>
                </td>
              </tr>
            </table>
            <div class="pad">
               <div v-if="shopcar.length>0"  class="over">
                    <span class="pull-left">商品总额：</span>
                    <strong class="pull-right red ">￥{{productPrize}}</strong>
               </div>
               <div v-if="shopcar.length>0"  class="over">
                    <span class="pull-left">运费( {{zhongliang}}kg)：</span>
                    <strong class="pull-right red">￥{{yunfei}}</strong>
               </div>
               <div v-if="shopcar.length>0"  class="over">
                    <span class="pull-left">总计： </span>
                    <strong class="pull-right red">￥{{totalPrize}}</strong>
               </div>
               <el-button class="mar" :disabled="shopcar.length==0" style="width:100%" @click="send" type="primary">立即采购</el-button>
            </div>
        </div>
    </div>
    <foot></foot>
    <el-dialog
      :visible.sync="detailDiv"
      width="900px"
      center>
       <div style="height:600px;overflow:auto;">
          <div class="over">
                <div class="bigImg"><img :src="imgUrl"></div>
                <ul class="smallImg">
                    <li v-for="item in smallList" @mouseover="getBigImg(item)"><img :src="item"></li>
                </ul>
                <div class="detailRight">
                    <div class="detailTitle">{{main.name}}</div>
                    <div>
                        <span class="red">￥<strong class="jg">{{main.price/100}}</strong></span>
                        淘宝价:<span class="scj">{{main.oldPrice/100}}</span>
                    </div>
                    <div class="mar">重量:{{main.weight/1000}}kg/件</div>
                    <div class="mar">
                        <el-input-number v-model="buynum" :min="1" :max="main.amount"></el-input-number>
                        &nbsp;&nbsp;库存：{{main.amount}}
                    </div>
                    <el-button class="mar" style="width:180px;" @click="add(main,buynum);detailDiv=false"  type="primary">加入礼品包</el-button>
                </div>
          </div>
          <div class="mar mainH">
                <div class="mainHICO"></div><span class="pull-left">产品介绍</span>
          </div>
          <div class="mar">
             <div v-html='main.detail'></div>
          </div>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import foot from './public/foot.vue'
import top from './public/top.vue'
import topMember from './public/topMember.vue'
export default {
  components: {
    foot,
    topMember,
    top
  },
  data() {
    return {
      list: [],
      page:1,
      size:16,
      sort:'id',
      direction:'asc',
      detailDiv:false,
      main:{},
      buynum:1,
      imgUrl:'',
      smallList:[],
      Hovcolor:'#c0c4cc',
      Hovcolor2:'#c0c4cc',
      Hovcolor3:'#c0c4cc',
      Hovcolor4:'#c0c4cc',
      Hovcolor5:'#c0c4cc',
      Hovcolor6:'#c0c4cc',
      shopcar:[],
      total:0,
      productPrize:'',
      zhongliang:'',
      yunfei:'',
      totalPrize:'',
      priceForm:JSON.parse(sessionStorage.getItem('priceForm'))
    };
  },
  mounted(){
    this.getList();
  },
  methods:{
    send(){
      sessionStorage.setItem('shopcar',JSON.stringify(this.shopcar));
      this.$router.push({
        path:'/buy',
        query: {
            buy:"0",
            productPrize:this.productPrize,
            zhongliang:this.zhongliang,
            yunfei:this.yunfei,
            totalPrize:this.totalPrize
        }
      })
    },
    getList() {
      let that = this;
      that.list=[];
      this.axios
        .get(
          // process.env.API_HOST+"goods?page="+(that.page-1)+"&size="+that.size+"&sort="+that.sort+"&direction="+that.direction,
          "/giftmall/goods?state=0&page="+(that.page-1)+"&size="+that.size+"&sort="+that.sort+"&direction="+that.direction,
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              // "domain": "taobao1.tianyalei.com",
              "domain": this.url,
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.list=response.data.data.list;
             that.total=response.data.data.totalCount;
             for(var i=0;i<that.list.length;i++){
                if(that.list[i].images==null){
                    that.list[i].images='../../static/nopic.jpg'
                }
             }
          }
        });
    },
    paixu(sort){
      this.sort=sort;
      if(sort=='price'){
          if(this.direction=='asc'){
              this.direction='desc';
              this.Hovcolor='#ec3440';
              this.Hovcolor2='#c0c4cc';
              this.Hovcolor3='#c0c4cc';
              this.Hovcolor4='#c0c4cc';
              this.Hovcolor5='#c0c4cc';
              this.Hovcolor6='#c0c4cc';
          }else{
            this.direction='asc';
              this.Hovcolor='#c0c4cc';
              this.Hovcolor2='#ec3440';
              this.Hovcolor3='#c0c4cc';
              this.Hovcolor4='#c0c4cc';
              this.Hovcolor5='#c0c4cc';
              this.Hovcolor6='#c0c4cc';
          }
      }else if(sort=='weight'){
        if(this.direction=='asc'){
              this.direction='desc';
              this.Hovcolor='#c0c4cc';
              this.Hovcolor2='#c0c4cc';
              this.Hovcolor3='#ec3440';
              this.Hovcolor4='#c0c4cc';
              this.Hovcolor5='#c0c4cc';
              this.Hovcolor6='#c0c4cc';
          }else{
            this.direction='asc';
              this.Hovcolor='#c0c4cc';
              this.Hovcolor2='#c0c4cc';
              this.Hovcolor3='#c0c4cc';
              this.Hovcolor4='#ec3440';
              this.Hovcolor5='#c0c4cc';
              this.Hovcolor6='#c0c4cc';
          }
      }else {
         if(this.direction=='asc'){
              this.direction='desc';
              this.Hovcolor='#c0c4cc';
              this.Hovcolor2='#c0c4cc';
              this.Hovcolor3='#c0c4cc';
              this.Hovcolor4='#c0c4cc';
              this.Hovcolor5='#ec3440';
              this.Hovcolor6='#c0c4cc';
          }else{
            this.direction='asc';
              this.Hovcolor='#c0c4cc';
              this.Hovcolor2='#c0c4cc';
              this.Hovcolor3='#c0c4cc';
              this.Hovcolor4='#c0c4cc';
              this.Hovcolor5='#c0c4cc';
              this.Hovcolor6='#ec3440';
          }
      }

      this.getList();
    },
    detail(id){
      this.detailDiv=true;
      this.buynum=1;
      let that = this;
      this.axios
        .get(
          // process.env.API_HOST+"goods/"+id,
          "/giftmall/goods/"+id,
          {
            headers: {
              "Content-Type": "application/x-www-form-urlencoded",
              "domain": this.url,
              // "domain": "taobao1.tianyalei.com",
              "Authorization":sessionStorage.getItem('token')
            }
          }
        )
        .then(function(response) {
          if (response.data.code == 200) {
             that.main=response.data.data;
             if(response.data.data.images){
                that.smallList=response.data.data.images.split(',');
                that.imgUrl=that.smallList[0]
             }else{
               that.smallList=['../../static/nopic.jpg'];
               that.imgUrl='../../static/nopic.jpg'
             }

          }
        });
    },
    add(item,num){
       let that=this;
       let isHave=that.shopcar.find((element) => (element.id == item.id));
       if(isHave){
         for(var i=0;i<that.shopcar.length;i++){
              if(item.id==that.shopcar[i].id){
                that.$set(that.shopcar[i],"num",Number(num)+Number(that.shopcar[i].num));
                that.$set(that.shopcar[i],"total",(parseFloat(item.price *100).toFixed(2)*that.shopcar[i].num)/10000);
              }
          }
       }else{
          that.$set(item,"num", num)
          that.$set(item,"total",(parseFloat(item.price *100).toFixed(2)*item.num)/10000);
          that.shopcar.push(item);
       }
       that.changePrize();
    },
    del(index){
      this.shopcar.splice(index,1);
      this.changePrize();
    },
    changePrize(){
      let that=this;
       that.zhongliang=(parseFloat(that.shopcar.reduce((num, next) => next.weight*next.num+num, 0))/1000).toFixed(2);
       that.productPrize=that.shopcar.reduce((num, next) => next.total+num, 0).toFixed(2);
       if(that.zhongliang<=0.5){
          that.yunfei=(that.priceForm.nearPrice1/100).toFixed(2);
       }else if(that.zhongliang>0.5&&that.zhongliang<=1){
          that.yunfei=(that.priceForm.nearPrice2/100).toFixed(2);
       }else if(that.zhongliang>1){
          that.yunfei=((Math.ceil(that.zhongliang-1))*that.priceForm.nearPrice3/100+that.priceForm.nearPrice2/100).toFixed(2);
       }
       that.totalPrize=(parseFloat(that.productPrize)+parseFloat(that.yunfei)).toFixed(2);
    },
    changeNum(id,val){
       for(var i=0;i<this.shopcar.length;i++){
          if(id==this.shopcar[i].id){
             this.$set(this.shopcar[i],"total",(parseFloat(this.shopcar[i].price *100).toFixed(2)*val)/10000);
             this.changePrize();
          }
       }
    },
    getBigImg(src){
      this.imgUrl=src;
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
        this.size=val;
        this.getList();
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        this.page=val;
        this.getList();
      }
  }
};
</script>

<style scoped>
.shaixuan {
  background:#eeeeee;text-align: center;
}
.shaixuan .el-col {
  cursor: pointer;
}
.shaixuan .caret-wrapper {
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    height: 34px;
    width: 24px;
    vertical-align: middle;
    cursor: pointer;
    overflow: initial;
    position: relative;
}
.shaixuan .sort-caret {
    width: 0;
    height: 0;
    border: 5px solid transparent;
    position: absolute;
    left: 7px;
}
.shaixuan .sort-caret.ascending {
    border-bottom-color: #c0c4cc;
    top: 5px;
}
.shaixuan .sort-caret.descending {
    border-top-color: #c0c4cc;
    bottom: 7px;
}
.shaixuan .sort-caret {
    width: 0;
    height: 0;
    border: 5px solid transparent;
    position: absolute;
    left: 7px;
}
.shoplist .el-col {
  margin-top:15px;
}
.shoplist img {
  width:100%;
  cursor: pointer;
}
.shopDes {
  color:#666;
  font-size:12px;
}
.gwc td,.gwc th {
  border-bottom:1px solid #eaeaea;
  padding:5px;
  font-size:12px;
  text-align: center;
}
.deleteBt {
  font-size:20px;
  font-weight: bold;
  color:#ec3440;
  cursor: pointer;
}
.el-dialog__header {
  padding:0px;
}
.bigImg {
  width:270px;
  height:270px;
  float:left;
  margin-right:15px;
}
.bigImg img {
  width:270px;
  height:270px;
}
.smallImg {
  width:50px;
  float:left;
}
.smallImg li {
  margin-bottom:10px;
}
.smallImg li img {
  width:50px;
  height:50px;
}
.detailRight {
  width:490px;
  float:right;
}
.detailTitle {
  font-size:25px;
  margin-bottom:15px;
}
.jg {
  font-size:30px;
}
.scj {
  text-decoration: line-through
}
.shoptitle  {
  height:40px;
  overflow: hidden;
}

</style>




